<template>
    <div class="main">
        <!--nav-->
        <div class="nav">
            <div class="logo">
                <img src="../assets/images/logo.png" alt="">
            </div>
            <div class="user_bicycle">
                <ul>
                    <router-link tag="li" to="/article"></router-link>
                    <li>|</li>
                    <li @click = "ShowPop()" :class=""></li>
                </ul>
            </div>
        </div>
        <!--content-->
        <div class="logo-title">
            <div class="title" v-show="!LoginShow">
                <p><img src="../assets/images/leave.png" alt=""></p>
                <p><img src="../assets/images/story.png" alt=""></p>
            </div>
        </div>

        <LyLogin @HidePopEvent="HidePop()"  v-show="LoginShow" :LoginShow="LoginShow" ref="LyLogin"></LyLogin>
        <LyTips></LyTips>
    </div>
</template>

<script>
    export default {
        name: "LyHome.vue",
        data(){
            return {
                LoginShow:false
            }
        },

        methods:{
            ShowPop(){
                this.LoginShow = true;
                this.$refs.LyLogin.Show();
            },
            HidePop(){
                let that = this;
                that.LoginShow = false;
            }
        }
    }
</script>

<style scoped>
    @import '../assets/css/home.css'; /*引入公共样式*/

</style>


