        <main>
            <div class="bounce" id="bounce">
                <div id="emitter" ref="emitter">
                    <div ref="emitWrap" class="emit-wrap" style="width:100%;height:100%;position:fixed;overflow:visible; z-index:5000; pointer-events:none;">
                        <div ref="emitDot" class="emit-dot" v-for="item in dotQuantity"></div>
                    </div>
                </div>
                <transition name="fade">
                    <div class="form-group" v-if="show" ref="group" id="group">
                        <div class="out" v-if="showOut" @click="Hide()"></div>
                        <form action="" class="drop login"  v-if='showL'  ref="formGroup">
                            <div class="form-title">登录</div>
                            <div class="input-item">
                                <input type="text" class="ipt" name="username" v-model="username"  placeholder="用户名" @keyup = 'checkIpt("username",username)'>
                                <p class="error" v-show="errors.username">{{errors.username}}</p>
                            </div>
                            <div class="input-item">
                                <input class="ipt" type="password" name="password" v-model="password"  placeholder="密码" @keyup = 'checkIpt("password",password)'>
                                <p class="error" v-show="errors.password">{{errors.password}}</p>
                            </div>
                            <div class="input-item remember-item">
                                <input type="checkbox"  id="remember">
                                <label for="remember">记住我</label>
                            </div><div class="input-item submit-item">
                            <input type="button"  value="登录" @click="submitForm()"></div>
                            <div  class="goReg">
                                <a href="#" @click="LoginToReg()">立即注册</a>
                            </div>
                        </form>
                        <form action="" class="drop register" id="form-group" ref="formGroup" v-if="!showL">
                            <div class="form-title">注册</div>
                            <div class="input-item">
                                <input class="ipt" type="text" name="username" v-model="username"  placeholder="用户名" @keyup = 'checkIpt("username",username)'>
                                <p class="error" v-show="errors.username">{{errors.username}}</p>
                            </div>
                            <div class="input-item">
                                <input class="ipt" type="text" name="password" v-model="password"  placeholder="密码" @keyup = 'checkIpt("password",password)'>
                                <p class="error" v-show="errors.password">{{errors.password}}</p>
                            </div>
                            <div class="input-item">
                                <input type="text" name="repassword" class="ipt" v-model="repassword"  placeholder="确认密码" @keyup = 'checkIpt("repassword",repassword)'>
                                <p class="error" v-show="errors.repassword">{{errors.repassword}}</p>
                            </div>
                            <div class="input-item">
                                <input type="text" class="ipt" name="email" v-model="email"  placeholder="电子邮箱" @keyup = 'checkIpt("email",email)'>
                                <p class="error" v-show="errors.email">{{errors.email}}</p>
                            </div>
                            <div class="input-item submit-item">
                            <input type="button" value="注册" @click="register()"></div>
                            <div  class="goReg">
                            <a href="#" @click = 'LoginToReg()'>立即登录</a>
                            </div>
                        </form>
                    </div>
                </transition>
            </div>
        </main>
        <!--遮罩层-->
        <div class="mark" v-if="show"  @click="Hide()"></div>


    </div>
</template>

<script>
    export default {
        name: "LyLogin",
        props:['LoginShow'],
        data(){
            return {
                amount : 2,
                first:false,
                emitterSize : 150,
                dotQuantity : 40,
                dotSizeMax : 100,
                dotSizeMin : 10,
                showL:true,
                show:false,
                showOut:false,
                clicked:true,
                errors: {},
                username: null,
                password: null,
                repassword:null,
                email:null,
                isValidate:true
             }
         },
         mounted(){

         },
        computed:{

        },
        methods:{
            //弹框显示
            Show(){
                // this.explode(this.$refs.emitter);
                this.show = true;
                this.clicked = false;
                this.showOut = false;
            },
            //弹框隐藏
            Hide(){
                if(this.clicked){
                    this.$emit('HidePopEvent')
                }
            },
            //登录与注册框实现切换
            LoginToReg(){
                this.showL = !this.showL;
                this.errors = {};
                this.username = null;
                this.password = null;
                if(this.email) this.email = null;
                if(this.repassword) this.repassword = null;
            },
            //气泡效果start
            explode(element){
                let explosion = this.createExplosion(this.$refs.emitWrap);
                var top = element.offsetTop;
                var left = element.offsetLeft;
                var width = element.offsetWidth;
                var height = element.offsetHeight;
                var This = this;
                TweenLite.set(This.$refs.emitWrap,{
                    x:left + width/2,
                    y:top+ height/2
                });
                explosion.restart();
            },
            createExplosion(){
                var This = this;
                let angle,length,dot,size;
                let tl = new TimelineLite({
                    onComplete:function(){
                        for(let i = 0;i<This.dotQuantity;i++) {
                            This.$refs.emitDot[i].style.cssText = '';
                        }
                        This.clicked = true;
                        This.showOut = true;
                    }
                });
                for(let i = 0;i<This.dotQuantity;i++){
                    dot = This.$refs.emitDot[i];
                    size = This.getRandom(This.dotSizeMin, This.dotSizeMax);
                    angle = Math.random() * Math.PI * 2;
                    length = Math.random() * (This.emitterSize / 2 - size / 2);
                    TweenLite.set(dot, {
                        x: Math.cos(angle) * length,
                        y: Math.sin(angle) * length,
                        width: size,
                        height: size,
                        xPercent: -50,
                        yPercent: -50,
                        force3D: true
                    });

                    tl.to(dot, 1 + Math.random(), {
                        opacity: 0,
                        x: Math.cos(angle) * length * 24,
                        y: Math.sin(angle) * length * 24
                    }, 0.1);
                }
                return tl;
            },
            //随机数（用于生成气泡）
            getRandom(min,max){
                return min + Math.random()*(max-min);
            },
            //气泡效果end
            checkForm(){
                if (this.username && this.password) {
                    return true;
                }
                this.errors = {};

                if (!this.username) {
                    this.$set(this.errors,'username', '请输入用户名');
                    this.isValidate = false;
                }
                if (!this.password) {
                    this.$set(this.errors,'password', '请输入密码');
                    this.isValidate = false;
                }else if(!this.CheckPwd(v)){
                    this.$set(this.errors,'password','密码为8-18位的数字和字母');
                    this.isValidate = false;

                }
                if (!this.repassword) {
                    this.$set(this.errors,'repassword', '请再次输入密码');
                    this.isValidate = false;
                }
                if (!this.email) {
                    this.$set(this.errors,'email', '请输入email');
                    this.isValidate = false;
                }

                return this.isValidate;

            },
            checkIpt(ele,v){
                if(ele == 'username'){
                    if(!v){
                        this.$set(this.errors,'username', '请输入用户名');
                        this.isValidate = false;
                    }else{
                        this.$set(this.errors,'username', '');
                        this.isValidate = true;
                    }
                }
                if(ele == 'password'){
                    if(!v){
                        this.$set(this.errors,'password','请输入密码');
                        this.isValidate = false;
                    }else if(!this.CheckPwd(v)){
                        this.$set(this.errors,'password','密码为8-18位的数字和字母');
                        this.isValidate = false;
                    }else{
                        this.$set(this.errors,'password','');
                        this.isValidate = true;
                    }
                }
                if(ele == 'repassword'){
                    if(!v){
                        this.$set(this.errors,'repassword','请重新输入密码');
                        this.isValidate = false;
                    }else if(this.repassword != this.password){
                        this.$set(this.errors,'repassword','两次输入密码不一致');
                    }else{
                        this.$set(this.errors,'repassword','');
                        this.isValidate = true;
                    }
                }
                if(ele == 'email'){
                    if(!v){
                        this.$set(this.errors,'email','请重新邮箱地址');
                        this.isValidate = false;
                    }else{
                        this.$set(this.errors,'email','');
                        this.isValidate = true;
                    }
                }
            },
            CheckPwd(v){
                //密码验证（数字、字母、8位以上）
                //密码必须包含数字和字母
                    if (v == null || v.length < 8 || v.length>16) {
                        return false;
                    }
                    var reg = new RegExp(/^(?![^a-zA-Z]+$)(?!\D+$)/);
                    if (reg.test(v))
                        return true;

            },
            submitForm(){
                if(this.checkForm() && this.isValidate){
                    this.$http.post('/users/login',{
                        username:this.username,
                        password:this.$md5(this.password)
                    }).then((res) => {
                        var data = res.data;
                        if(data.status == '0'){
                            if(!data.msg){
                                this.$emit('HidePopEvent');
                                this.$bus.$emit('tips', { show: true, title: '登录成功'});
                                // this.$router.push({path:'/article'});
                            }else{
                                this.$bus.$emit('tips',{show:true,title:data.msg});
                            }
                        }
                    });
                }
            },
            register(){
                if(this.checkForm() && this.isValidate){
                    this.$http.post('/users/reg',{
                        username:this.username,
                        password:this.$md5(this.password),
                        email:this.email
                    }).then((res) => {
                         console.log(res)
                    });
                }
            }

        }
    }
</script>

<style scoped>
    /*登录框*/
    .boom{position:fixed;top:0px;left:0px;height:100%;width:100%;overflow:hidden;}
    .hideBoom{opacity:0;visibility:hidden;}
    .showBoom{opacity:1;visibility:visible;}
    main{text-align:center;width:380px;height:100%;margin-left:calc(50% - 190px);}
    #emitter {width: 20px;height: 20px;display: block;position: absolute;top:150px;left: 50%;-webkit-transform: translateX(-50%) translateY(-12px);transform: translateX(-50%) translateY(-12px);}
    .emit-dot {background-color: #fff;border-radius: 999px;position: absolute;}
    .dot-end{width:0;height:0; transition : none; transform : none;}
    .bounce {position: relative;margin: 140px 0;width:380px;transition:top 1.2s linear;-webkit-transition:top 1.2s linear;}
    .form-group{width:100%;}
    .form-group form{width:100%;padding:50px;box-sizing: border-box;border-radius:30px;}
    .drop {position: absolute;border-radius:30px;padding:50px;width:100%;left: 0;background:rgba(256,256,256,0.5);-webkit-transition: -webkit-transform 1.4s ease-in, background 0.3s ease-in;transition: transform 1.4s ease-in, background 0.3s ease-in;}
    .form-title{font-size:24px;color:#5d585e;margin-bottom:40px;}
    .input-item{width:100%;height:45px;margin-bottom:30px;}
    .input-item input[type=text],.input-item input[type=password]{width:100%;height:100%;background:rgba(256,256,256,0.4);border:none;outline:none;border-radius:3px;padding-left:20px;box-sizing: border-box;}
    .remember-item{text-align:left;padding-left:10px;color:#68676D;font-size:14px;margin-bottom:0;}
    .submit-item input{width:100%;height:100%;background:#68676d;font-size:18px;outline:none;border:none;color:#ffffff;opacity:0.8;cursor:pointer;}
    .goReg{text-align:right;}
    .goReg a{color:#68676D;font-size:14px;}
    p.error{text-align:left;font-size:12px;height:20px;line-height:20px;color:red;}



    /*登录框遮罩*/
    .mark{position:fixed;background:rgba(0,0,0,0.1);width:100%;height:100%;top:0;left:0;z-index:-1;}
    .emitWrap{width:100%;height:100%;position:fixed; left:0; top:0; overflow:visible; z-index:5000; pointer-events:none;}
    .fade-enter-active, .fade-leave-active {transition: opacity 1.2s;}
    .fade-enter, .fade-leave-to{opacity: 0;}
    .out{width:15px;height:15px;position:absolute;top:20px;right:20px;background:url('../../assets/images/icon_X.png') no-repeat center center;background-size:cover;cursor:pointer;z-index:999999;}
</style>


