<template>
    <transition name = "fade">
        <div class="tips" v-show = "tips.show">
            <h3>{{tips.title}}</h3>
        </div>
    </transition>

</template>

<script>
    export default {
        name: "LyTips.vue",
        data(){
          return {
              tips:{
                  show:false,
                  title:''
              }
          }
        },
        methods:{
          receive(){
              this.$bus.$on('tips',(data) => {
                  this.tips = data;
              })
          }
        },
        mounted(){
          this.receive();
        },
        watch:{
            tips(){
                setTimeout(()=>{
                    this.tips.show = false;
                },1500)
            }
        }
    }
</script>

<style scoped>
    .tips{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 999;
        width: 100%;
        height: 100%;
        color: #fff;
    }
    .tips h3{
        padding: 15px 20px;
        font-size: 14px;
        line-height: 14px;
        background-color: rgba(0,0,0,0.6);
        border-radius: 4px;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to{
        opacity: 0;
    }
</style>


