import Vue from 'vue'
import Router from 'vue-router'
import LyHome from '@/components/LyHome'
import Article from '@/components/article'
import '@/assets/css/common.css'/*引入公共样式*/
import '@/assets/js/jquery.min.js'
import '@/assets/js/TweenMax.js'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'LyHome',
      component: LyHome
    },
      {
          path: '/article',
          name: 'Article',
          component: Article
      }
  ]
})



